<template>
    <div class="taber">
       <ul class="flex flex-between flex-item">
           <li  class="flex-one" v-for="(item,index) in taberlist" :class="{active:def==index}" @click="onchange(index)" :key="index">{{item}}</li>
       </ul>
    </div>
</template>
<script>
export default {
    name:"taber",
    data(){
        return {
            def:0
        }
    },
    props:{
        taberlist:{
            type:Array,
            default:function(){
                return ["一级分类","一级分类"]
            }
        }
    },
    beforeMount(){

    },
    methods:{
        onchange(index){
            this.def=index;
            this.$emit("change",index);
        }
    }
}
</script>
<style lang="scss" scoped>
.taber{
    ul{
        height: 100%;
        text-align: center;
        li{
            display: table;
            color: #ffffff;
            height: 100%;
            padding: 0.2rem 0;
            &.active{
                background: url("../assets/img/ui 9.20_bg_chosen.png") no-repeat top center/70%;
                color: #00FFD0
            }
        }
    }
}
</style>
